#game {
  width: 1280px;
  height: 720px;
  box-sizing: content-box;
  border: 4px solid #fefefeb2;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.game__menu {
  width: 320px;
  height: 384px;
  background-image: url("../menu/menu.png");
  justify-content: center;
  align-items: center;
  animation-name: slidein;
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(.425, 1.55, .335, .545);
  display: flex;
  position: absolute;
  top: 160px;
  left: 480px;
}

.game__menu--slideout {
  animation-name: slideout;
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(.18, .8, .255, .96);
  animation-fill-mode: forwards;
}

.game__buttons {
  flex-direction: column;
  display: flex;
}

.game__buttons > :not(:last-child) {
  margin-bottom: 20px;
}

.game__button {
  color: #fff;
  text-align: center;
  width: 192px;
  height: 64px;
  background-image: url("../menu/btn-normal.png");
  padding-top: 6px;
  font-family: Monogram;
  font-size: 48px;
}

.game__button:hover {
  background-image: url("../menu/btn-hover.png");
}

.left-chain {
  width: 64px;
  height: 256px;
  background-image: url("../menu/left-chain.png");
  position: absolute;
  bottom: 288px;
  left: 16px;
}

.right-chain {
  width: 64px;
  height: 256px;
  background-image: url("../menu/right-chain.png");
  position: absolute;
  bottom: 288px;
  right: 16px;
}

@keyframes slidein {
  from {
    top: -380px;
  }

  to {
    top: 160px;
  }
}

@keyframes slideout {
  to {
    top: -380px;
  }
}

.game__loader-container {
  width: 80%;
  display: none;
}

.game__loader-text {
  color: #fff;
  font-family: Monogram;
  font-size: 56px;
}

.game__loader-bar {
  border: 4px solid #fefefeb2;
  border-radius: 8px;
  margin-top: 20px;
  padding: 8px;
}

.game__loader-progress {
  width: 0;
  height: 12px;
  background-color: #fff;
}

@font-face {
  font-family: Monogram;
  src: url("../fonts/monogram.ttf");
}

* {
  box-sizing: border-box;
  cursor: url("../ui/cursor.png"), auto;
  margin: 0;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #141412;
  justify-content: center;
  align-items: center;
  display: flex;
}

